import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Customer } from 'src/app/model/customer';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { CustomerServiceService } from 'src/app/service/customer-service.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-create-customer',
  templateUrl: './create-customer.component.html',
  styleUrls: ['./create-customer.component.scss']
})
export class CreateCustomerComponent implements OnInit {
  formCreate: FormGroup;
  customer: Customer = new Customer();
  @Output() myclick = new EventEmitter();

  validation_messages = {
    'cusName': [
      { type: 'required', message: 'Tn khng c  trng' },
      { type: 'pattern', message: 'Tn khch hng khng hp l' }
    ],
    'cusId': [
      { type: 'required', message: 'M khch hng khng c  trng' },
      { type: 'pattern', message: 'M khch hng khng ng nh dng KH-XXXX vi X l number' }
    ],
    'cusType': [
      { type: 'required', message: 'Loi khch khng c  trng' },
    ],
    'birthday': [
      { type: 'required', message: 'Ngy sinh khng c  trng' }
    ],
    'idCard': [
      { type: 'required', message: 'CMND khng c  trng' },
      { type: 'pattern', message: 'CMND khng ng nh dng' }
    ],
    'numberPhone': [
      { type: 'required', message: 'SDT khng c  trng' },
      { type: 'pattern', message: "S in thoi khng ng nh dng" }
    ],
    'email': [
      { type: 'required', message: 'Email khng c  trng' },
      { type: 'pattern', message: 'Email khng ng nh dng abc@abc.abc' }
    ],
    'address': [
      { type: 'required', message: 'a ch khng c  trng' },
    ]
  };
  constructor(private fb: FormBuilder, private router: Router, private customerService: CustomerServiceService) { }

  ngOnInit() {
    this.formCreate = this.fb.group({
      cusId: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/^(?:\s*)\KH-\d{4}(?:\s*)$/)
      ])],
      cusName: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/^([\p{Lu}][\p{Ll}]{1,8})(\s([\p{Lu}]|[\p{Lu}][\p{Ll}]{1,10})){0,5}$/u)
      ])],
      cusType: ['', Validators.compose([
        Validators.required
      ])],
      numberPhone: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/(090|091|\+(84)?91|\+(84)?90)+([0-9]{7})\b/)
      ])],
      idCard: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/^([\d]{9}|[\d]{12})$/)
      ])],
      email: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/^[a-z][a-z0-9_\.]{4,32}@[a-z0-9]{2,}(\.[a-z0-9]{2,4}){1,2}$/)
      ])],
      address: ['', Validators.compose([
        Validators.required
      ])],
      birthday: ['', Validators.compose([
        Validators.required,
      ])]
    }
    );
  }
  onSubmit(form) {
    if (this.formCreate.valid) {
      this.customer.cusId = form.cusId;
      this.customer.cusName = form.cusName;
      this.customer.cusType = form.cusType;
      this.customer.numberPhone = form.numberPhone;
      this.customer.idCard = form.idCard;
      this.customer.email = form.email;
      this.customer.address = form.address;
      this.customer.birthday = form.birthday;
      this.customerService.createCustomer(this.customer).subscribe(
        data => {
          this.myclick.emit();
          window.alert('Thm thnh cng');
        }
      );
    }
  }
  reset() {
    this.formCreate.reset();
  }
}

