import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Employee } from 'src/app/model/employee';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { EmployeeService } from 'src/app/service/employee.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-create-employee',
  templateUrl: './create-employee.component.html',
  styleUrls: ['./create-employee.component.scss']
})
export class CreateEmployeeComponent implements OnInit {

  formCreate: FormGroup;
  employee: Employee = new Employee();
  @Output() myclick = new EventEmitter();

  validation_messages = {
    'empName': [
      { type: 'required', message: 'Tn khng c  trng' },
      { type: 'pattern', message: 'Tn nhn vin khng hp l' }
    ],
    'empId': [
      { type: 'required', message: 'M nhn vin khng c  trng' },
      { type: 'pattern', message: 'M nhn vin khng ng nh dng NV-XXXX vi X l number' }
    ],
    'salary': [
      { type: 'number', message: 'Lng phi ln hn 0' },
      { type: 'pattern', message: 'Lng phi l s' }
    ],
    'birthday': [
      { type: 'required', message: 'Ngy sinh khng c  trng' }
    ],
    'idCard': [
      { type: 'required', message: 'CMND khng c  trng' },
      { type: 'pattern', message: 'CMND khng ng nh dng' }
    ],
    'numberPhone': [
      { type: 'required', message: 'SDT khng c  trng' },
      { type: 'pattern', message: "S in thoi khng ng nh dng" }
    ],
    'email': [
      { type: 'required', message: 'Email khng c  trng' },
      { type: 'pattern', message: 'Email khng ng nh dng abc@abc.abc' }
    ],
    'address': [
      { type: 'required', message: 'a ch khng c  trng' },
    ]
  };
  constructor(private fb: FormBuilder, private router: Router, private service: EmployeeService) { }

  ngOnInit() {
    this.formCreate = this.fb.group({
      empId: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/^(?:\s*)\NV-\d{4}(?:\s*)$/)
      ])],
      empName: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/^([\p{Lu}][\p{Ll}]{1,8})(\s([\p{Lu}]|[\p{Lu}][\p{Ll}]{1,10})){0,5}$/u)
      ])],
      salary: ['', Validators.compose([
        Validators.required,
        numberValidator,
        Validators.pattern(/^[-+]?[0-9]*\.?[0-9]+$/)
      ])],
      numberPhone: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/(090|091|\+(84)?91|\+(84)?90)+([0-9]{7})\b/)
      ])],
      idCard: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/^([\d]{9}|[\d]{12})$/)
      ])],
      email: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/^[a-z][a-z0-9_\.]{4,32}@[a-z0-9]{2,}(\.[a-z0-9]{2,4}){1,2}$/)
      ])],
      address: ['', Validators.compose([
        Validators.required
      ])],
      birthday: ['', Validators.compose([
        Validators.required,
      ])]
    }
    );
  }
  onSubmit(form) {
    if (this.formCreate.valid) {
      this.employee.empId = form.empId;
      this.employee.empName = form.empName;
      this.employee.salary = form.salary;
      this.employee.numberPhone = form.numberPhone;
      this.employee.idCard = form.idCard;
      this.employee.email = form.email;
      this.employee.address = form.address;
      this.employee.bitrhay = form.birthday;
      this.service.createEmployee(this.employee).subscribe(
        data => {
          this.myclick.emit();
          window.alert('Thm thnh cng');
        }
      );
    }
  }
  reset() {
    this.formCreate.reset();
  }
}
function numberValidator(form: FormControl) {
  if (form.value * 1 <= 0) {
    return { number: true };
  }
  return null;
}
