import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Service } from 'src/app/model/service';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { ServiceFuramaService } from 'src/app/service/service-furama.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-create-service',
  templateUrl: './create-service.component.html',
  styleUrls: ['./create-service.component.scss']
})
export class CreateServiceComponent implements OnInit {

  formCreate: FormGroup;
  service: Service = new Service();
  @Output() myclick = new EventEmitter();

  validation_messages = {
    'serviceName': [
      { type: 'required', message: 'Tn khng c  trng' },
      { type: 'pattern', message: 'Tn dch v khng hp l' }
    ],
    'serviceId': [
      { type: 'required', message: 'M dch v khng c  trng' },
      { type: 'pattern', message: 'M dch v khng ng nh dng DV-XXXX vi X l number' }
    ],
    'area': [
     
      { type: 'pattern', message: 'din tch phi l s' },
      { type: 'number', message: 'Din tch phi ln hn 0' }
    ],
    'numberFloor': [

      { type: 'pattern', message: 'S tng phi l s nguyn dng' },
      { type: 'number', message: 'S tng phi ln hn 0' }
    ],
    'maxPerson': [

      { type: 'pattern', message: 'S ngi phi l s nguyn dng' },
      { type: 'number', message: 'S ngi phi ln hn 0' }
    ],
    'price': [

      { type: 'number', message: 'Ga phi ln hn 0' },
      { type: 'pattern', message: 'Ga phi l s' }
    ]
  };
  constructor(private fb: FormBuilder, private router: Router, private furamaService: ServiceFuramaService) { }

  ngOnInit() {
    this.formCreate = this.fb.group({
      serviceId: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/^(?:\s*)\DV-\d{4}(?:\s*)$/)
      ])],
      serviceName: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/^([\p{Lu}][\p{Ll}]{1,8})(\s([\p{Lu}]|[\p{Lu}][\p{Ll}]{1,10})){0,5}$/u)
      ])],
      area: ['', Validators.compose([
        Validators.required,
        numberValidator,
        Validators.pattern(/^[-+]?[0-9]*\.?[0-9]+$/)
      ])],
      numberFloor: ['', Validators.compose([
        Validators.required,
        numberValidator,
        Validators.pattern(/^([\d])/)
      ])],
      price: ['', Validators.compose([
        Validators.required,
        numberValidator,
        Validators.pattern(/^[-+]?[0-9]*\.?[0-9]+$/)
      ])],
      maxPerson: ['', Validators.compose([
        Validators.required,
        numberValidator,
        Validators.pattern(/^([\d])/)
      ])],
      status: []
    }
    );
  }
  onSubmit(form) {
    if (this.formCreate.valid) {
      this.service.serviceId = form.serviceId;
      this.service.serviceName = form.serviceName;
      this.service.area = form.area;
      this.service.numberFloor = form.numberFloor;
      this.service.price = form.price;
      this.service.maxPerson = form.maxPerson;
      this.service.status = form.status;
      this.furamaService.createService(this.service).subscribe(
        data => {
          this.myclick.emit();
          window.alert('Thm thnh cng');
        }
      );
    }
  }
  reset() {
    this.formCreate.reset();
  }
}
function numberValidator(form: FormControl) {
  if (form.value * 1 <= 0) {
    return { number: true };
  }
  return null;
}
